
<style scoped>

</style>


<template>
    <div>
        <el-card v-if="powerlist">
            <el-table
                :data='powerlist'
            >
                <el-table-column 
                    prop='id' label='#'
                />
                <el-table-column 
                    prop='authName' label='权限名称'
                />
                <el-table-column 
                    prop='path' label='路径'
                />
                <el-table-column 
                    prop='level' label='权限等级'
                >
                    <template slot-scope="s">
                        <el-tag
                            :type='s.row.level=="2"?"warning":s.row.level?"success":""'
                        >{{ s.row.level | num_characher }}</el-tag>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
    </div>
</template>


<script>
export default {
    name:'powerlist',
    data() {
        return {
            powerlist:null,
        };
    },
    created(){

    },
    mounted(){
        this.oninit()
    },
    //props:{},//[]
    methods:{
        async oninit(){
            let {data:res}=await this.$http.get(
                `/rights/list`,{}
            )
            console.log('权限列表',res)
            if(res.data){
                this.powerlist=res.data
            }
        }
    },
    computed:{

    },
    watch:{

    },
    filters:{
        num_characher(v){
            let tail='级',head=''
            switch(v){
                case '0':
                    head= '一'
                break;
                case '1':
                    head= '二'
                break;
                case '2':
                    head= '三'
                break;
            }
            return head+tail
        }
    },
    directives:{

    },
    components:{

    },
};
</script>